<template>
  <div id="headlines">
    <div id="headline" v-for="content in getArticle"
                        v-bind:key="content.id">
      <img id="headline-img" :src="content.img" :alt="content.img_text">
        <div id="text">
          <transition>
          <h1 id="title" @click="$router.push({name: 'single', query: {index: content.index}})" transition>
            {{content.title}}
          </h1>
          </transition>
          <br>
          {{content.headline_text}}
        </div>
        <div id="clear"/>
    </div>
  </div>
</template>

<script>
import Single from '@/components/single'

export default {
  components: {
    Single
  },
  computed: {
    getArticle: function () {
      switch (this.$route.query['type']) {
        case 'pc':
          return this.articlePc
        case 'ps4':
          return this.articlePs4
        case 'xbox':
          return this.articleXbox
        case 'switch':
          return this.articleSwitch
        case 'vr':
          return this.articleVr
        case 'ar':
          return this.articleAr
        default:
          return this.articleList
      }
    }
  }
}
</script>

<style>
div#headlines{
  padding-bottom:25px;
}

/*タイトルについて*/
h1#title{
  margin-top: 0px;
  display: inline-block;
}

h1#title{
  transition: all 0.19s ease;
}

h1#title:hover{
  border-bottom: 3px solid black;
  cursor: pointer;
}
/* h1#title.fade-enter,h1#title.fade-leave-to{
  opacity: 0;
} */

/*見出しについて*/
div#headline {
  margin-bottom:10px;
  padding: 15px;
  border: 10px solid rgb(128, 116, 116);
  /* border-radius: 15px; */
}

img#headline-img {
  width: 300px;
  float:left;
  padding-right: 1em;
}

div#clear{
  clear: both;
}
</style>



