<template>
  <div id="single">
    <h1 id="content-h1">{{articleList[index].title}}</h1>
    <a :href="articleList[index].img"><img :src="articleList[index].img"></a>
    <p v-html="articleList[index].text"></p>
  </div>
</template>

<script>
export default{
  computed: {
    index () {
      return this.$route.query['index']
    }
  }
}
</script>

<style>
div#single{
  padding-bottom:100px;
  min-height: 500px;
}

div#single img {
  width: 500px;
  float:left;
  padding-right:40px;
  padding-bottom:40px;
}

div#single p{
  line-height: 175%;
}
</style>



