<template>
  <div id="header">
    <div id="site_title"  @click="$router.push({name: 'content',query: {type: 'home'}})">ニッチなゲーム情報</div>
    <div id="contact" @click.stop="$router.push({name: 'contact'})">お問い合わせ</div>
    <div id="global_navi">
      <ul class="navi-ul">
        <li class="tab">
          <router-link @click.stop.native="noneMethod()" :to="{name: 'content',query:{type: 'pc'}}">
            <strong>PC</strong>
          </router-link>
        </li>
        <li class="tab">
          <router-link @click.stop.native="noneMethod()" :to="{name: 'content',query:{type: 'ps4'}}">
            <strong>PS4</strong>
          </router-link>
        </li>
        <li class="tab">
          <router-link @click.stop.native="noneMethod()" :to="{name: 'content',query:{type: 'xbox'}}">
            <strong>XBOX</strong>
          </router-link>
        </li>
        <li class="tab">
          <router-link @click.stop.native="noneMethod()" :to="{name: 'content',query:{type: 'switch'}}">
            <strong>SWITCH</strong>
          </router-link>
        </li>
        <li class="tab">
          <router-link @click.stop.native="noneMethod()" :to="{name: 'content',query:{type: 'vr'}}">
            <strong>VR</strong>
          </router-link>
        </li>
        <li class="tab">
          <router-link @click.stop.native="noneMethod()" :to="{name: 'content',query:{type: 'ar'}}">
            <strong>AR</strong>
          </router-link>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default{
  methods: {
    noneMethod: function () {}
  }
}
</script>

<style>
div#header {
  width: 1200px;
  background-color: violet;
  margin-left: auto;
  margin-right: auto;
  background: linear-gradient(400deg, #e4a972, #9941d8);
}
div#site_title:hover{
  cursor: pointer;
}

/*サイトのタイトル*/
div#site_title {
  font-size: 30px;
  padding-left: 10px;
  width: 300px;
  margin-right: auto;
}

/*お問い合わせ*/
div#contact {
  font-size: 14px;
  width: 100px;
  margin-left: auto;
  position: relative;
  bottom: 40px;
}
div#contact:hover{
  cursor: pointer;
}

/*ナヴィ*/
div#global_navi {
  height: 30px;
  background-color: rosybrown;
}

ul.navi-ul {
  text-align: center;
  font-size: 18px;
}

li a {
  display: block;
  width: 100%;
  text-decoration: none;
  transition: all 0.2s ease;
  background-color: rgb(82, 82, 218);
  color:white;
}

li a:hover{
  background-color: white;
  color: rgb(82, 82, 218);
}

li.tab {
  margin: 0px;
  margin-top: 4px;
  width: calc(960px / 7);
  display: inline-block;
  background-color: yellow;
}
</style>



