import Vue from 'vue'
import Router from 'vue-router'

import headline from '@/components/headline'
import single from '@/components/single'
import contact from '@/components/contact'
import conf from '@/components/confirmation'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      name: 'content',
      type: 'default',
      path: '/docs',
      component: headline
    },
    {
      name: 'single',
      index: 0,
      path: '/docs/main',
      component: single
    },
    {
      name: 'contact',
      path: '/docs/contact',
      component: contact
    },
    {
      name: 'conf',
      email: '',
      text: '',
      path: '/docs/conf',
      component: conf
    }
  ]
})



