<template>
  <div id="contact_page">
    <h1>お問い合わせ</h1>

    <p id="email" class="c_p">メールアドレス</p>
    <input type="text" name="email" v-model="email">

    <p id="text" class="c_p">内容</p>
    <textarea id="input_id" cols="50" rows="12" v-model="text" spellcheck="false"></textarea>
    <div id="center">
      <transition>
        <p id="text_length" v-if="isLongText" class="color">
          残り {{200 - text.length}}文字
        </p>
        <p id="text_length" v-else>
          残り {{200 - text.length}}文字
        </p>
      </transition>
      <button @click="$router.push({name: 'conf', params: {email: email, text: text}})" v-if="!isLongText">送信内容の確認</button>
      <button @click="$router.push({name: 'conf', params: {email: email, text: text}})" v-else disabled>送信内容の確認</button>

    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      email: '',
      text: '',
      isLongText: false
    }
  },
  computed: {
    enableText: function () {
      if (this.$route.params.enable === void 0) {
        return true
      } else {
        return this.$route.params.enable
      }
    }
  },
  watch: {
    text: function (newText, oldText) {
      if (0 <= 200 - newText.length) {
        this.isLongText = false
      } else {
        this.isLongText = true
      }
    }
  }
}
</script>

<style>
color.fade-enter-active, color.fade-leave-active {
  transition: opacity 0.5s;
}
color.fade-enter, color.fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}

.color{
  color: red;
}

p#text_length {
  transition: all 0.25s ease;
}

div#contact_page {
  font-size: 16px;
}

textarea {
  font-family: inherit;
  font-size: 100%;
}

p {
  margin-left: 30%;
  display: inline-block;
}

input,textarea {
  margin-right: 20%;
  margin-left: 2%;
  width: 400px;
}

textarea#input_id {
  margin-left: calc(2% + 80px);
}

div#center {
  text-align: center;
}
div#center p {
  margin-left: 40%;
  margin-top: 0px;
  display: block;
}
div#center button {
  margin-top: 50px;
  margin-bottom: 100px;
}
</style>



